<script>
import Vue from 'vue'
import Bus from '@/common/js/bus.js'
import { depth, heyuesell, heyuemultiple } from '@/api/user'
import { objKeySort, sgin } from '@/utils/auth'
import { DropdownMenu, DropdownItem, Icon, Toast } from 'vant'
Vue.use(DropdownMenu).use(Icon).use(DropdownItem).use(Toast)
export default {
  // props
  // props: {
  //   // 主分类激活索引
  //   defaultActive: {
  //     type: Number,
  //     default: 0
  //   }
  // },
  components: {},
  data () {
    return {
      isNav: 0,
      value2: 'a',
      isShow: false,
      spanActive1: 1, // 指数判断
      depthList: [],
      current_price_usd: '',
      spanActive: 0,
      option1: [
        { text: '选择倍数', value: 0 }
        // { text: '1倍', value: 1 },
        // { text: '2倍', value: 2 },
        // { text: '3倍', value: 3 },
        // { text: '4倍', value: 4 }
      ],
      heyuemultipleData: {
        u_id: '10' // int用户ID
      },
      // 合约参数
      heyuesellData: {
        u_id: '10', // int用户ID
        type: '看涨', // 类型（看涨、平空）
        market_price: '0.1778', // 市场价格
        margin: 'coin', // 保证金
        fee: 'coin', // 手续费
        multiple: 'coin', // 倍数
        lots: 'coin', // 手数
        symbol: 'btc', // 币种
        paypass: '' // 交易密码
      },
      shoushu: [], // 手数返回字段
      value1: 0,
      depthData: {
        market: 'depth',
        symbol: 'btc'
      }, // 盘口对象
      // 手数返回字段
      option2: [
      ]
    }
  },
  created () {
    this.heyuesellData.u_id = sessionStorage.getItem('u_id') // 合约提交参数
    this.heyuemultipleData.u_id = sessionStorage.getItem('u_id') // 倍数
    this.depthFun()
    this.heyuemultipleFun()
  },
  methods: {
    // 点击看涨看跌
    userRange (type) {
      // if()
      console.log(this.value1, 'this.value1')
      if (this.value1 === 0) {
        this.$toast({ message: '请选择倍数', duration: 1500 })
        return
      }
      this.heyuesellData.lots = this.spanActive1 // 手数
      // this.heyuesellData.symbol = 'btc' // 币种
      var margin = ''
      console.log(this.heyuesellData.market_price, 'this.heyuesellData.market_price')
      var fee = ''
      if (type === 3) {
        margin = (this.heyuesellData.market_price / this.heyuesellData.multiple) * this.heyuesellData.lots * 1 + this.heyuesellData.market_price * 0.0024 * this.heyuesellData.lots / 1
        fee = (this.heyuesellData.market_price * 0.008 * this.heyuesellData.lots).toFixed(4)
      } else if (type === 4) {
        margin = (this.heyuesellData.market_price / this.heyuesellData.multiple) * this.heyuesellData.lots * 1 - this.heyuesellData.market_price * 0.0024 * this.heyuesellData.lots / 1
        fee = (this.heyuesellData.market_price * 0.008 * this.heyuesellData.lots).toFixed(4)
      }
      console.log(margin, 'margin')
      this.heyuesellData.type = type
      this.heyuesellData.margin = margin.toFixed(4)
      this.heyuesellData.fee = fee
      console.log(margin, '保证金', fee, '手续费')
      Bus.$emit('getTarget', this.heyuesellData, '111')
      Bus.$emit('showBox', true, '2222')
      // this.heyuesellFun()
    },
    // 调用倍数倍率
    heyuemultipleFun () {
      var that = this
      var data = objKeySort(this.heyuemultipleData)
      var token = sgin(data)
      this.heyuemultipleData.token = token
      heyuemultiple(this.heyuemultipleData).then(res => {
        // console.log(res, '合约倍数指数')
        if (res.code === 0) {
          for (const [i, v] of res.data.beishu.entries()) {
            console.log(v, i)
            var array = {}
            array.text = v.value + '倍'
            array.value = v.value
            that.option1.push(array)
          }
          for (const [ii, vv] of res.data.showshu.entries()) {
            console.log(ii, vv)
            var obj = {}
            obj.text = vv.value
            obj.value = vv.value
            console.log(obj, 'obj')
            that.option2.push(obj)
          }
          // console.log(that.option1, '倍数')
          // console.log(that.option2, '手数')
          this.spanActive1 = that.option2[0].text
          delete that.heyuemultipleData.token
        }
      }).catch((err) => {
        console.log(err)
        delete that.heyuemultipleData.token
      })
    },
    // 点击选择指数
    userIndex (type) {
      this.spanActive1 = type
      console.log(type, '指数', this.spanActive1)
    },
    navClick (type) {
      this.spanActive = type
    },
    heyuesellFun () {
      var that = this
      var data = objKeySort(this.heyuesellData)
      var token = sgin(data)
      this.heyuesellData.token = token
      heyuesell(this.heyuesellData).then(res => {
        console.log(res)
        if (res.code === 0) {
          that.isShow = false
          // this.depthList = res.data
          // console.log('盘口列表1', res.data.bids)
          delete that.heyuesellData.token
        }
      }).catch((err) => {
        console.log(err)
        that.isShow = false
        delete that.heyuesellData.token
      })
    },
    ccc (value) {
      console.log('type', value)
      var that = this
      for (const [i, v] of that.option1.entries()) {
        if (v.value === value) {
          console.log('等于了', i, v, v)
          that.heyuesellData.multiple = v.value
        }
      }
    },
    FunText (type) {
      var text = Number(type)
      var conTxt = text.toFixed(4)
      // console.log(conTxt, '212111')
      return conTxt
    },
    depthFun () {
      var that = this
      var data = objKeySort(this.depthData)
      var token = sgin(data)
      this.depthData.token = token
      depth(this.depthData).then(res => {
        if (res.code === 0) {
          this.depthList = res.data
          // console.log('盘口列表1', res.data.bids)
          delete that.depthData.token
        }
      }).catch((err) => {
        console.log(err)
        delete that.depthData.token
      })
    },
    toOrder () {
      this.$router.push({
        path: '/personal/order',
        query: {
        }
      })
    },
    ButNav (type) {
      this.isNav = type
    }
  },
  mounted () {
    Bus.$on('marketPrice', marketPrice => {
      this.heyuesellData.market_price = marketPrice
    })
    Bus.$on('symbol', symbol => {
      var s = symbol
      var ss = s.split('/')
      this.heyuesellData.symbol = ss[0].toLowerCase()
    })
  }
}
</script>
